
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: "app",
directives: {
TransferDom
},
components: {
XHeader,
ViewBox,
Group,
Cell,
Confirm,
Toast
},
mounted() {},
methods: {

toClear() {
this.clearToast = true;
this.cache = "";
},

toLogin() {
this.$router.push({
path: "/login/login"
});
},
toAddress() {
this.$router.push({
path: "/address/address"
});
},

toOur() {
this.$router.push({
path: "/about/about"
});
},

toUserSet() {
this.$router.push({
path: "/userSet/userSet"
});
}
},
data() {
return {

cache: 5,

clearToast: false
};
}
};



